body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  min-height: 100vh;
  margin: 0;
  overflow: hidden;
  background-image: url(//assets2.iknow.jp/_assets/apps/common/sky-bg-9a9e5c46f4d6ed5431ecfc9553c57e3dcc96d9fcd3ba89aa6cb04394c710d9d1.jpg);
  background-color: #389EE8;
  background-size: cover;
}

.button--medium {
  width: 72px;
  height: 14px;
  padding: 10px;

  line-height: 14px;
  border-radius: 14px;
}


.button--teal {
  /* padding: 100px; */
  background: #0178CD;
}

.carousel {
  padding: 24px;
}

.card {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -277px 0px 0px -465px;
  padding: 15px;
  width: 900px;
  height: 524px;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  background: rgba(255, 255, 255, 0.4);
  box-shadow: 0 3px 6px rgba(0,0,0,0.15);
}

.card__panel {
  height: 40px;
  display: flex;
}

.card__panel__logo {
  background: 0 0 no-repeat;
  background-image: url(/Flash/static/media/flashpopz-icon.23387a19.png);
  background-size: contain;
  margin-top: -3px;
  width: 89px;
  height: 50px;
  position: relative;
}

.card__panel__banner {
  display: table;
  width: 100%;
  height: 100%;
  margin-left: 15px;
  margin-right: 5px;
  border-radius: 5px;
  background-color: #0178CD;
  box-shadow: 0px 0px 4px rgba(0,0,0,0.15) inset;
  text-align: left;
  padding-left: 15px;
}

.card__panel__banner span {
  display: table-cell;
  vertical-align: middle;
}

.card__panel__control-panel {
  float: right;
  text-align: right;
}

.card__screens {
  height: 435px;
  clear: both;
  background-color: #0178CD;
  border-radius: 8px;
  box-shadow: 0px 0px 4px rgba(0,0,0,0.15) inset;
}

.card__navigation {
  margin-top: 8px;
}

.card__navigation--right {
  float: right;
}

.card__navigation--left {
  float: left;
}

span, p, h1, h2 {
  color: #fff;
  font-family: "Helvetica Neue", "Helvetica", "Arial", "DejaVu Sans", "Kozuka Gothic Pro R", "MS P Gothic", "Hiragino Kaku Gothic Pro", "Osaka", "\30E1\30A4\30EA\30AA", "Meiryo", "\30D2\30E9\30AE\30CE\89D2\30B4   Pro W3", "\FF2D\FF33   \FF30\30B4\30B7\30C3\30AF", sans-serif;
  font-weight: bold;
}

.icon {
  width: 40px;
  height: 40px;
}

.icon--cancel {
    background-color: #3F6078;
    -webkit-mask: url(/Flash/static/media/baseline-cancel-24px.77fca812.svg) no-repeat center;
    mask: url(/Flash/static/media/baseline-cancel-24px.77fca812.svg) no-repeat center;
}

